/* Container for the tilted card */
.tilted-card-figure {
  position: relative;
  width: 100%;
  height: 100%;
  perspective: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Mobile warning alert */
.tilted-card-mobile-alert {
  position: absolute;
  top: 1rem;
  text-align: center;
  font-size: 0.875rem;
  display: none;
}

@media (max-width: 640px) {
  .tilted-card-mobile-alert {
    display: block;
  }
  .tilted-card-caption {
    display: none;
  }
}

/* Inner container for 3D transformations */
.tilted-card-inner {
  position: relative;
  transform-style: preserve-3d;
}

/* Image styling for the tilted card */
.tilted-card-img {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  border-radius: 15px;
  will-change: transform;
  transform: translateZ(0);
}

/* Overlay styling */
.tilted-card-overlay {
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translateX(-50%) translateZ(30px);
  z-index: 2;
  max-width: 80%;
  width: auto;
  padding: 6px 10px;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  border-radius: 8px;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  backdrop-filter: blur(5px);
  pointer-events: none;
  white-space: nowrap;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, transform 0.2s ease-out;
}

/* Show overlay on hover */
.tilted-card-inner:hover .tilted-card-overlay {
  opacity: 1;
}

/* Caption styling */
.tilted-card-caption {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 4px;
  background-color: #fff;
  padding: 4px 10px;
  font-size: 10px;
  color: #2d2d2d;
  opacity: 0;
  z-index: 3;
}

/* Progress bar container styling */
.progress-bar-container {
  position: absolute;
  bottom: -30px;
  left: 40px;
  height: 5px;
  background-color: #ccc;
  border-radius: 3px;
  transform-origin: center center;
  overflow: hidden;
  opacity: 1;
  z-index: 2;
  width: 60%;
  transition: transform 0.2s ease-out;
}

/* Progress bar fill styling */
.progress-bar-fill {
  background-color: #0084ff;
  border-radius: 3px;
  z-index: 2;
  will-change: transform;
}
